/*------------------------------------------------------------------------- */
/*----------------------------- MAIN STYLES ------------------------------- */
/*------------------------------------------------------------------------- */
a:hover, a:focus {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  width: 100%;
  height: 100%;
}

.is-hidden {
  display: none;
}

.row:before, .row:after {
  content: "";
  display: table;
}
.row:after {
  clear: both;
}

body {
  min-width: 320px;
  font-family: "opensans-semibold-webfont", Arial, sans-serif;
  font-size: 16px;
  color: #111;
  line-height: 1.6;
  background-color: #111;
}

.wrapper {
  overflow: hidden;
  margin: 0 auto;
  max-width: 1190px;
  min-height: 100%;
  padding-bottom: 50px;
}

.footer {
  margin-top: -50px;
  height: 50px;
  font-family: "sourcesanspro-semibold-webfont", Arial, sans-serif;
  font-size: 0.875em;
  line-height: 20px;
  background-color: #fff;
}
.footer-wrap {
  margin: 0 auto;
  max-width: 1190px;
}

/* HEADER */
.header {
  position: relative;
  z-index: 1;
  height: 50px;
  line-height: 20px;
}
.header:before, .header:after {
  content: "";
  display: table;
}
.header:after {
  clear: both;
}
.header .main-nav {
  float: left;
}
.header .logo {
  position: absolute;
  left: 0;
  top: 0;
}
.header .search {
  float: right;
}
.header .icon {
  float: left;
  margin-top: 2px;
  margin-right: 7px;
  width: 20px;
  height: 15px;
  fill: #fff;
}
.header .icon.icon-babes {
  stroke-width: 12;
  stroke: #fff;
}
.header .icon.icon-lifestyle {
  stroke-width: 1;
  stroke: #fff;
}
.header .icon.icon-entertainment {
  stroke-width: 15;
  stroke: #fff;
}

.main-nav {
  margin-left: 252px;
}
.main-nav ul {
  font-size: 0.9375em;
  color: #fff;
  text-transform: uppercase;
}
.main-nav ul:before, .main-nav ul:after {
  content: "";
  display: table;
}
.main-nav ul:after {
  clear: both;
}
.main-nav ul li {
  float: left;
}
.main-nav ul li a {
  display: block;
  padding: 15px;
  transition: all 0.2s;
}
.main-nav ul li a:hover {
  background-color: #D1221E;
  transition: all 0.2s;
}

.logo a {
  display: block;
}
.logo a img {
  width: 227px;
  height: 123px;
  vertical-align: middle;
}

.search {
  font-family: "sourcesanspro-semibold-webfont", Arial, sans-serif;
}
.search:before, .search:after {
  content: "";
  display: table;
}
.search:after {
  clear: both;
}
.search-inp {
  float: left;
  margin-top: 10px;
  padding: 5px 0;
  width: 0;
  height: 30px;
  font-family: "sourcesanspro-semibold-webfont", Arial, sans-serif;
  font-size: 1em;
  color: #111;
  background-color: #fff;
  transition: width 0.5s;
}
.search-inp.is-active {
  padding: 5px;
  width: 200px;
  transition: width 0.5s;
}
.search-btn {
  float: left;
  margin-left: 15px;
  padding: 15px;
  font-size: 1em;
  color: #fff;
  line-height: 20px;
  text-transform: uppercase;
  background-color: transparent;
  transition: all 0.2s;
}
.search-btn:hover {
  background-color: #D1221E;
  transition: all 0.2s;
}

/* FOOTER */
.footer:before, .footer:after {
  content: "";
  display: table;
}
.footer:after {
  clear: both;
}
.footer .copyright {
  margin-top: 15px;
  float: left;
}
.footer .foot-nav {
  float: right;
}

.foot-nav ul {
  text-transform: uppercase;
}
.foot-nav ul:before, .foot-nav ul:after {
  content: "";
  display: table;
}
.foot-nav ul:after {
  clear: both;
}
.foot-nav ul li {
  float: left;
}
.foot-nav ul li a {
  display: block;
  padding: 15px;
  transition: all 0.2s;
}
.foot-nav ul li a:hover {
  color: #fff;
  background-color: #D1221E;
  transition: all 0.2s;
}

/* MAIN */
.main-news {
  margin-bottom: 40px;
  font-family: "opensans-extrabold-webfont", Arial, sans-serif;
  font-size: 2.25em;
  line-height: 38px;
}
.main-news a {
  display: block;
}
.main-news__img {
  max-height: 380px;
  overflow: hidden;
}
.main-news__img img {
  width: 100%;
  height: auto;
  vertical-align: middle;
}
.main-news__desc {
  padding: 20px 60px 10px;
  height: 120px;
  background-color: #fff;
}
.main-news__desc .icon {
  float: right;
}
.main-news__desc p {
  overflow: hidden;
}


